<template>
    <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="cart.html">
            Корзина
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Заказ оформлен <span>№ {{ order.id }}</span>
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <p class="cart__message">
            Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту придет письмо с&nbsp;деталями заказа. 
            Наши менеджеры свяжутся с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
          </p>

          <ul class="dictionary">
            <li class="dictionary__item">
              <span class="dictionary__key">
                Получатель
              </span>
              <span class="dictionary__value">
               {{order.name}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Адрес доставки
              </span>
              <span class="dictionary__value">
                {{order.address}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Телефон
              </span>
              <span class="dictionary__value">
                {{order.phone}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Email
              </span>
              <span class="dictionary__value">
                {{order.email}}
              </span>
            </li>
            <li class="dictionary__item">
              <span class="dictionary__key">
                Способ оплаты
              </span>
              <span class="dictionary__value">
                картой при получении
              </span>
            </li>
          </ul>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItems v-for="item in order.basket.items" :key="item.productId" :item="item" />
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{order.basket.items.length}}</b>  {{ textSkins }} на сумму <b>{{numberFormat(order.totalPrice)}} ₽</b></p>
          </div>
        </div>
      </form>
    </section>
  </main>
</template>
<script>
import {mapGetters} from 'vuex';
import OrderItems from '@/components/OrderItems';
 export default {
  data(){
      return {
        words: ['товар', 'товара', 'товаров'],
      }
    },
    components: {OrderItems: OrderItems},
    computed: {
      ...mapGetters({order: 'orderDataItems'}),
      textSkins(totalAmount, wordsArr) {
        totalAmount = this.order.basket.items.length;
        wordsArr = this.words;
        if (totalAmount % 10 === 1 && totalAmount % 100 !== 11) { 
        return wordsArr[0];
        } else if (totalAmount % 10 >= 2 && totalAmount % 10 <= 4 && (totalAmount % 100 < 10 || totalAmount % 100 >= 20)) {
        return wordsArr[1];            
        }
        return wordsArr[2];
      },
    },
    methods: {
      numberFormat(value){
            return new Intl.NumberFormat().format(value);
      },
    },
    created(){
      if(this.$store.state.orderInfo && this.$store.state.orderInfo.id === this.$route.params.id){
        return;
      }
      this.$store.dispatch('loadOrderInfo', this.$route.params.id)
    },
        
  }
</script>
