<template>
    <li class="cart__item product">
        <div class="product__pic">
            <img :src="item.product.image" width="120" height="120" srcset="img/phone-square-3@2x.jpg 2x" :alt="item.product.title">
        </div>
        <h3 class="product__title">
            {{ item.product.title }}
        </h3>
        <span class="product__code">
            Артикул: {{ item.product.id }}
        </span>

        <div class="product__counter form__counter">
            <button type="button" aria-label="Убрать один товар" @click.prevent="decrement()">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
            </button>

            <input type="text" v-model.number="amount" name="count">

            <button type="button" aria-label="Добавить один товар" @click.prevent="increment()">
                <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
            </button>
        </div>

        <b class="product__price">
            {{ numberFormat(item.product.price * item.amount) }} ₽
        </b>

        <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct()">
            <svg width="20" height="20" fill="currentColor">
                <use xlink:href="#icon-close"></use>
            </svg>
        </button>
    </li>
</template>

<script>
  
  import { mapMutations } from 'vuex';
  import { mapActions } from 'vuex';

  export default {
    
    props: ['item'],
    computed: {
        amount: {
            get(){
                return this.item.amount;
            },
            set(value){
                this.$store.dispatch('updateCartProductAmount', {productId: this.item.productId, amount: value});
            }
        },
       
    },
    methods: {
        ...mapActions(['deleteCartProduct']),
        deleteProduct(){
            
            this.deleteCartProduct({productId: this.item.productId});
        },
        increment(){
        this.$emit('update:item', this.amount += 1);
        },
        decrement(){
          if(this.amount > 0) {
            this.$emit('update:item', this.amount -= 1);
          }
        },
        numberFormat(value){
            return new Intl.NumberFormat().format(value);
        },
        loadProduct(){
        
        axios.get(API_BASE_URL + `/api/products/` + this.$route.params.id)
             .then(response => this.productData = response.data)
             .catch(() => this.productLoadingFailed = true)
             .then(() => this.productLoading = false);
      }
    }
  }
</script>