<template>
    <div class="form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="decrement()">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
            </svg>
      </button>

      <input type="text" v-model.number="currentCount" name="count">

      <button type="button" aria-label="Добавить один товар"  @click.prevent="increment()">
        <svg width="12" height="12" fill="currentColor">
            <use xlink:href="#icon-plus"></use>
            </svg>
      </button>
    </div>
</template>

<script>

export default {
    data(){
      return {
        currentCount: 1,
      }
    },
    props: ['amount'],
    watch: {
      amount(value){
        this.currentCount = value;
      },
    },
    methods: {
      increment(){
        this.$emit('update:amount', this.currentCount += 1);
      },
      decrement(){
        if(this.currentCount > 0) {
          this.$emit('update:amount', this.currentCount -= 1);
        }
      },
    },
}
</script>
