<template>

  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link" href="index.html">
            Каталог
          </a>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info" v-model="textSkins">
        {{totalAmount}} {{ textSkins }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ numberFormat(totalPrice)}} ₽</span>
          </p>

          <router-link tag="button" class="cart__button button button--primery" type="submit" :to="{name: 'order'}" v-if="products.length >= 1">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>

</template>

<script>

import {mapGetters} from 'vuex';
import CartItem from '@/components/CartItem';

  export default {
    data(){
      return {
        words: ['товар', 'товара', 'товаров'],
      }
    },
    components: {CartItem: CartItem},
    computed: {
      ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount'}),
      textSkins(totalAmount, wordsArr) {
        totalAmount = this.totalAmount;
        wordsArr = this.words;
        if (totalAmount % 10 === 1 && totalAmount % 100 !== 11) {
        return wordsArr[0];
        } else if (totalAmount % 10 >= 2 && totalAmount % 10 <= 4 && (totalAmount % 100 < 10 || totalAmount % 100 >= 20)) {
        return wordsArr[1];
        }
        return wordsArr[2];
      },
    },
    methods: {
      numberFormat(value){
            return new Intl.NumberFormat().format(value);
      }
    }

  }

</script>
